* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #fff;
  font-family: monospace, "Verdana";
}
#main-container {
  margin: 0 auto;
  width: 50%;
  min-height: 100vh;
  background: #333;
}

/* Info Starts */
header {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.4);
}
.hero {
  width: 100%;
  height: 400px;
  margin: 0 auto 10px;
  border-radius: 10px;
}
header > h1 {
  color: #fff;
  font-size: 2.4rem;
  font-weight: 600;
  margin: 0 0 10px;
}
header > p {
  color: ghostwhite;
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.links {
  width: 60%;
  height: 40px;
  background: #fff;
  border-radius: 12px;
  text-align: center;
  padding: 8px 0;
  text-decoration: none;
  color: #1a1a1a;
  font-weight: 600;
  margin-bottom: 10px;
}
.links:hover {
  background: #1a1a1a;
  color: #686868;
  transition: 0.3s all;
}

/* About Starts */

#about,
#interest {
  padding: 28px 48px;
}
#about{
    margin-bottom: 12px;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
}
#interest{
    margin-bottom: 32px;
}
#about h2,
#interest h2 {
  letter-spacing: 1px;
  margin-bottom: 6px;
  font-weight: 800;
}
footer {
  background: #111;
}
.social-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80px;
}
.social-icons > li {
  margin: 16px 12px;
  list-style: none;
}

.social-icons > li > a {
  display: inline-block;
  text-decoration: none;
}

@media (max-width: 810px) {
    #main-container {
        width: 70%;
    }
}
@media (max-width:510px) {
    .social-icons {
        flex-wrap: wrap;
        width: 100%;
        height: auto;
    }
    
}
